/*result viewer table*/
#resultViewer {
  text-align: center;
  font-family: 'Fjalla One', sans-serif;
  border: 3px solid black;
  width: 100%;
  margin: 0 auto;
}

#resultViewer th {
  padding: 5px;
  background-color: var(--ion-color-pink);
  border: 3px solid black;
} 

#resultViewer td {
  padding: 5px;
  border: 3px solid black;
} 

#checkboxSection ion-item, #checkboxSection1 ion-item {
  --padding-end: 0px;
  --inner-padding-end: 0px;
}

#checkboxSection {
  float: right;
  width: auto;
  margin-top: 26px;
}

#checkboxSection1 {
  float: right;
  width: auto;
}

#checkbox {
  --background-checked: var(--ion-color-pink);
  --border-color-checked: var(--ion-color-pink);
  --border-radius: 50px;
  --size: 20px;
}

#checkboxTitle {
  font-family: 'Fjalla One', sans-serif;
  margin-right: 10px;
}

#popularityDesc {
  font: normal 80% "Fjalla One" !important;
  margin-top: 10px;
}

#popularityCount {
  font-family: 'Fjalla One', sans-serif;
  clear: right;
  float: right;
  margin-top: 10px;
}

@media (max-width: 768px) {
  /* CSS that should be displayed if width is equal to or less than 800px goes here */
  #checkbox {
    --size: 15px;
  }

  #checkboxSection {
    margin-top: 17px;
  }

  #checkboxTitle {
    font: normal 80% "Fjalla One" !important;
  }

  #popularityDesc {
    font: normal 60% "Fjalla One" !important;
    margin-top: 5px;
  }

  #popularityCount {
    font: normal 80% "Fjalla One" !important;
    margin-top: 0px;
  }
  
}

